import React, {
    useEffect
} from "react";
import "css/Slider.scss";
import "css/public.scss";
import slider from "js/slider"
import debug from "debug.js";
import { faCaretLeft, faCaretRight } from "@fortawesome/free-solid-svg-icons"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
//fa-caret-left
const BannerSlider = (props) => {
    let btnwh = sltype.btnwh[props.type-1];
    let slider_cnt = (props.images.length > 1 ? props.images.length+1 : props.images.length);
    let slider_w = sltype.slider_wh('w',props.type,props.wh,btnwh);
    let slider_h = sltype.slider_wh('h',props.type,props.wh,btnwh);

    useEffect(()=>{
        slider.auto("#slider-"+props.id+" ul");
        console.log(sltype.two.frame_w(props.id));
    });
    const css = {
        slider_container: {
            width:props.wh[0],
            height:props.wh[1],
            flexDirection: sltype.needColumn.includes(props.type) ? 'column' : 'row',
        },
        slider: {
            width:slider_w,
            height:slider_h
        },
        btn: {
            width:btnwh[0],
            height:btnwh[1],
        },
        ul: {
            width: slider_w*slider_cnt
        },
        li: {
            width:slider_w
        }
    }
    const initSlider = (slider_cnt, images) => {
        let slider_item = [];
        debug("start init");
        let idx
        for(idx=0 ; idx < props.images.length; idx++){
            slider_item.push(
                <li className={"slider-img "+images[idx]} 
                    data-order={idx+1} key={idx}>
                </li>
            )
        }
        // 마지막에 fake를 붙여서 연속된 슬라이더를 만든다.
        if(images.length >= 2){
            slider_item.push(
                <li className={"slider-img "+images[0]} 
                    data-order="0" key={idx}>
                </li>
            )
        }
        return slider_item;
    }
    const sliderBtn = (event, dir) => {
        let frame_tag = '.slider ul';
        slider.sliderMove(event,dir,frame_tag);
    }
    
    return ( 
        <>
        <div id={"slider-"+props.id}className="slider-container" style={css.slider_container}>
            <div className="slider" style={css.slider}>
                <ul className="slider-ani" style={css.ul} data-idx="1" data-cnt={slider_cnt}>
                    {initSlider(slider_cnt, props.images)}
                </ul> 
            </div> 
            <div className="sltype-two" style={css.btn}>
                <button className="button-clear left">
                    <FontAwesomeIcon icon={faCaretLeft} color={'#e09879'} size={'4x'}/>
                </button>
                <div className="sltype-two-frame">
                    <ul className="slider-ani" data-idx="1" data-cnt={slider_cnt}>
                    </ul>
                </div>
                <button className="button-clear right">
                    <FontAwesomeIcon icon={faCaretRight} color={'#e09879'} size={'4x'}/>
                </button>
            </div>
        </div> 
        </>
    );
}

BannerSlider.defaultProps = {
    images: ["bg-black","bg-green","bg-red"],
    wh: [600,150],
    type: 2,
    id:1
}

const sltype = {
    //{sltype.one(-1,'test',sliderBtn,css.btn)}
    btnwh : [[30,0],[1055,80]],
    needColumn : [2],
    one :(dir, txt, func, css) => {
        let add_class = dir==-1 ? 'slider-back' : 'slider-prev';
        return (
            <button className={"sltype-one "+add_class}
                onClick={(e) => func(e,dir)} style={css}> 
            {txt}
            </button> 
        )
    },
    two : {
        frame_w : (id) => {
            let frame = document.querySelector("#slider-"+id+" .sltype-two-frame");
            return frame;
        },
    },
    slider_wh : (key,type,wh,btnwh) => {
        let ret = 0
        if(key === 'w') {
            if(type === 1) {
                ret = wh[0] - btnwh[0]*2;
            }
            else {
                ret = wh[0];
            }
        }
        else if(key === 'h') {
            if(type === 1) {
                ret = wh[1];
            }
            else if(type === 2) {
                ret = wh[1] - btnwh[1];
            }
        }
        return ret;
    }
}

export default BannerSlider;
